body {
    background-color: #f1f2f3;
}
.app {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
  
    width: 300px;
    height: 100%;
    margin:0 auto;
    padding: 0 40px 50px 40px;
    background-color: #d5cfb6;
    
  }

  .app h1 {
      text-align: center;
      font-size: 2em;
      color: #3c3c20;
  }
  .app .list-of-planets {
    flex-basis: 100%;
    text-align: center;
  }
  
  .app .list-of-planets .planet {
    margin: 10px 0;
    padding: 10px 0;
    background-color: #f1f2f3;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    border: rgba(102, 102, 102, 0.2) 1px solid;
    border-radius: 10px;
    cursor: pointer;
  }

  .app .list-of-planets .planet .planet-name{
    color:#3c3c20;
    font-size: 1.1em;
    font-weight: 500;
  }
  
  .app .list-of-planets .planet .information {
    display: block;
  }

  .app .list-of-planets .planet .information p {
    text-align: left;
    margin-left:10px;
    color: #3c3c20;
  }

  .app .list-of-planets .planet .information.hidden {
    display: none;
  }


  .app .buttons {
    width: 100%;
  }
  
  .app .buttons .buttons-list {
    list-style: none;
    padding:0;
    text-align: center;
  }
  
  .app .buttons .buttons-list .button {
    display: inline-block;
    padding: 5px 11px;
    margin: 0 2px;
    background-color: #f1f2f3;
    border: rgba(102, 102, 102, 0.2) 1px solid;
    border-radius: 100%;
    cursor: pointer;
  }
  
  .app .buttons .buttons-list .button.active {
    background-color:#9d9974;
  }